import React, { FC } from "react";
import { graphql, PageProps } from "gatsby";
import { SimpleGrid, Text } from "@chakra-ui/react";

import { Layout } from "../components/layout";
import { LinkCard } from "../components/ui";

const ProjectsPage: FC<PageProps<Queries.ProjectsPageQuery>> = ({ data }) => {
  const projects = data.allMdx.nodes;

  return (
    <Layout title="Projects">
      <SimpleGrid columns={[1, 2, 3]} gap={4}>
        {projects.map((project) => {
          return (
            <LinkCard
              key={project.id}
              title={project.frontmatter?.title || project.fields?.slug}
              to={project.fields?.slug || ""}
              image={
                project.frontmatter?.thumbnailImage?.childImageSharp?.gatsbyImageData ||
                project.frontmatter?.featuredImage?.childImageSharp?.gatsbyImageData
              }
            >
              <Text
                dangerouslySetInnerHTML={{
                  __html: project.frontmatter?.description || project.excerpt || "",
                }}
              />
            </LinkCard>
          );
        })}
      </SimpleGrid>
    </Layout>
  );
};

export default ProjectsPage;

export const pageQuery = graphql`
  query ProjectsPage {
    allMdx(sort: { frontmatter: { title: ASC } }, filter: { internal: { contentFilePath: { regex: "//content/projects/.+/" } } }) {
      nodes {
        id
        excerpt
        fields {
          slug
        }
        frontmatter {
          title
          description
          featuredImage {
            childImageSharp {
              gatsbyImageData(width: 300)
            }
          }
          thumbnailImage {
            childImageSharp {
              gatsbyImageData(width: 300)
            }
          }
        }
      }
    }
  }
`;
